import { Trans } from '@lingui/macro'
import { Sentry } from '@speedlo/sentry'
import React from 'react'

import { FormInputStyled } from '../../../atoms/FormInputStyled'
import { MarginBox } from '../../../atoms/MarginBox'
import { appConfig } from '../../../config'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'

export const PasswordResetForm: React.FC = () => {
  const inputRef = React.useRef<HTMLInputElement | null>(null)
  const [isSubmitting, setSubmitting] = React.useState(false)
  const { enqueueError, enqueueSuccess } = useSnackBar()
  const recoverPassword = async () => {
    setSubmitting(true)
    const { success, message } = await requestRecovery(inputRef.current!.value)
    if (success) {
      enqueueSuccess(
        i18n.t`Email to recover your password has been successfully sent.`,
      )
    } else {
      enqueueError(message)
    }
    setSubmitting(false)
  }
  return (
    <>
      <FormInputStyled
        placeholder={i18n.t`Login name`}
        ref={inputRef}
        disabled={isSubmitting}
      />
      <MarginBox margin={`${Spacing.Middle} 0 0 0`}>
        <TouchButton
          disabled={isSubmitting}
          onClick={recoverPassword}
          color={Colors.green}
          height="auto"
        >
          <Trans>Recover password</Trans>
        </TouchButton>
      </MarginBox>
    </>
  )
}

async function requestRecovery(loginName: string) {
  const url = `${appConfig.endpoint}/users/reset_password?login=${loginName}`
  const fetch: TFetch = window.fetch
  try {
    const response = await fetch(url, {
      headers: {
        Accept: 'application/json',
      },
    })
    return response.json()
  } catch (err) {
    Sentry.withScope(scope => {
      scope.setLevel(Sentry.Severity.Debug)
      scope.setExtra('loginName', loginName)
      Sentry.captureException(err)
    })
    return {
      success: false,
      message: i18n.t`Unknown error while requesting password recovery`,
    }
  }
}
